}
h1{
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-style: bold;
    margin: 0;
    padding: 0;
}
#background-video {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
  }
